	module.exports = {"pathContext":{"docHtml":"<h1>pseudomap</h1>\n<p>A thing that is a lot like ES6 <code>Map</code>, but without iterators, for use\nin environments where <code>for..of</code> syntax and <code>Map</code> are not available.</p>\n<p>If you need iterators, or just in general a more faithful polyfill to\nES6 Maps, check out <a href=\"http://npm.im/es6-map\">es6-map</a>.</p>\n<p>If you are in an environment where <code>Map</code> is supported, then that will\nbe returned instead, unless <code>process.env.TEST_PSEUDOMAP</code> is set.</p>\n<p>You can use any value as keys, and any value as data.  Setting again\nwith the identical key will overwrite the previous value.</p>\n<p>Internally, data is stored on an <code>Object.create(null)</code> style object.\nThe key is coerced to a string to generate the key on the internal\ndata-bag object.  The original key used is stored along with the data.</p>\n<p>In the event of a stringified-key collision, a new key is generated by\nappending an increasing number to the stringified-key until finding\neither the intended key or an empty spot.</p>\n<p>Note that because object traversal order of plain objects is not\nguaranteed to be identical to insertion order, the insertion order\nguarantee of <code>Map.prototype.forEach</code> is not guaranteed in this\nimplementation.  However, in all versions of Node.js and V8 where this\nmodule works, <code>forEach</code> does traverse data in insertion order.</p>\n<h2>API</h2>\n<p>Most of the <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Map\">Map\nAPI</a>,\nwith the following exceptions:</p>\n<ol>\n<li>A <code>Map</code> object is not an iterator.</li>\n<li><code>values</code>, <code>keys</code>, and <code>entries</code> methods are not implemented,\nbecause they return iterators.</li>\n<li>The argument to the constructor can be an Array of <code>[key, value]</code>\npairs, or a <code>Map</code> or <code>PseudoMap</code> object.  But, since iterators\naren't used, passing any plain-old iterator won't initialize the\nmap properly.</li>\n</ol>\n<h2>USAGE</h2>\n<p>Use just like a regular ES6 Map.</p>\n<pre><code class=\"language-javascript\">var PseudoMap = require('pseudomap')\n\n// optionally provide a pseudomap, or an array of [key,value] pairs\n// as the argument to initialize the map with\nvar myMap = new PseudoMap()\n\nmyMap.set(1, 'number 1')\nmyMap.set('1', 'string 1')\nvar akey = {}\nvar bkey = {}\nmyMap.set(akey, { some: 'data' })\nmyMap.set(bkey, { some: 'other data' })\n</code></pre>","docPath":"argo-ci/node_modules/pseudomap/readme","proj":"argo-ci"}}

